<!-- ////////////////////////////////////////////////// -->
<!--                      TEMPLATE                      -->
<!-- ////////////////////////////////////////////////// -->

<template>
  <HeaderHome />

  <h1>Forum Articles</h1>

  <div class="bloc" v-for="article in articles" v-bind:key="article.titre">
    <h3>
      <a id="lienProfil" title="Voir le profil" @click="goToProfil(article.auteurId)">{{
        article.auteur
      }}</a>
    </h3>

    <h4>{{ article.titre }}</h4>
    <p>{{ article.contenu }}</p>

    <div id="timeStamp">
      <p>Publié le {{ article.date }}</p>
      <a class="bouton" @click="partagerArticle()">Partager</a>
    </div>
  </div>
</template>

<!-- ////////////////////////////////////////////////// -->
<!--                       SCRIPT                       -->
<!-- ////////////////////////////////////////////////// -->

<script>
import HeaderHome from "@/components/HeaderHome.vue";

// Axios
const axios = require("axios");
const instance = axios.create({
  baseURL: "http://localhost:3000/api/",
});

export default {
  name: "HomeView",
  path: "/",
  props: [""],
  data() {
    return {
      articles: []
    };
  },
  mounted() {
    // Remplissage des data avec les infos API
    instance
      .get(`article/`)
      .then((response) => {
        let i = 0;
        for (i = 0; i < response.data.length; i += 1) {
          let article = response.data[i];
          let rowDate = article.createdAt;
          let date = rowDate.substr(0, 10);
          this.articles.push(
            { 
            "titre": article.title, 
            "contenu": article.text, 
            "auteur": article.User.firstName, 
            "auteurId": article.UserId,
            "date": date 
            });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    goToProfil: function (userId) {
      this.$router.push(`/profil/${userId}`);
    },
    partagerArticle: function () { ///////////////////////////////////
      console.log("partage");
    }
  },
  components: { HeaderHome },
};
</script>

